const subject = [
    { group: "a", title: "你保持精力充沛吗？" },
    { group: "a", title: "你感觉情绪高涨、处事乐观吗？" },
    { group: "a", title: "你说话时声如洪钟、铿锵有力吗？" },
    { group: "a", title: "你记忆力好吗？" },
    { group: "b", title: "你容易气短（呼吸短促，接不上气）吗？" },
    { group: "b", title: "你喜欢安静，懒得说话吗？" },
    { group: "b", title: "你容易心慌吗？" },
    { group: "b", title: "你容易头晕或站起时晕眩吗？" },
    { group: "c", title: "你感觉手脚发凉吗？" },
    { group: "c", title: "你胃脘部、背部、腰膝怕冷吗？" },
    { group: "c", title: "你吃（喝）凉的会感到不舒服、怕吃凉的吗？" },
    { group: "c", title: "你比身边人更容易感冒吗？" },
    { group: "d", title: "你感觉身体、脸上发热吗？" },
    { group: "d", title: "你容易便秘或大便干燥吗？" },
    { group: "d", title: "你感到眼睛干涩吗？" },
    { group: "d", title: "你感到口干咽燥总想喝水吗？" },
    { group: "e", title: "你的腹部肥满松软吗？" },
    { group: "e", title: "你有头部油脂分泌多的情况吗？" },
    { group: "e", title: "你感到胸闷或腹部胀满吗？" },
    { group: "e", title: "你平时痰多，特别是咽喉总感觉有痰堵着吗？" },
    { group: "f", title: "你容易生痤疮或疖（jie）子吗？" },
    { group: "f", title: "你小便时尿道有发热感、尿色浓（深）吗？" },
    { group: "f", title: "你大便时粘滞不爽有解不尽的感觉吗？" },
    { group: "f", title: "你白带颜色发黄吗？（限女性回答）" },
    { group: "f", title: "你的阴囊部位潮湿吗？（限男性回答）" },
    { group: "g", title: "你的皮肤会不知不觉的出现青紫瘀斑吗？" },
    { group: "g", title: "你容易有黑眼圈吗？" },
    { group: "g", title: "你容易健忘（忘事）吗？" },
    { group: "g", title: "你的面色黯淡容易出现褐斑吗？" },
    { group: "h", title: "你容易感到精神紧张、焦虑不安吗？" },
    { group: "h", title: "你会无缘无故叹气吗？" },
    { group: "h", title: "你容易感到害怕或受到惊吓吗？" },
    { group: "h", title: "你咽喉部位有异物感，且吐不出、咽不下吗？" },
    { group: "i", title: "你没有感冒时也会鼻塞、流涕吗？" },
    { group: "i", title: "你容易过敏（对药物、食物、气味、花粉或在季节气候变化时）吗？" },
    { group: "i", title: "你的皮肤容易起荨麻疹（风团、风疹）吗？" },
    { group: "i", title: "你的皮肤一抓就红，并出现抓痕吗？" },
];

export default class Questionnaire extends React.Component {

    constructor(props) {
        super(props);
        var answer = [];
        if (this.props.location.state) {
            subject.map((item, index) => {
                if (item.title.indexOf('限女性回答') > -1 && this.props.location.state.sex[0] != 0) {
                    subject.splice(index, 1)
                    answer.push({ "index": index, "val": 1, "group": item.group })
                } else if (item.title.indexOf('限男性回答') > -1 && this.props.location.state.sex[0] != 1) {
                    subject.splice(index, 1);
                    answer.push({ "index": index, "val": 1, "group": item.group })
                } else {
                    answer.push({ "index": index, "val": 1, "group": item.group })
                }
            })
        }

        this.state = { answer }
    }
    render() {
        return (
            <div id="page-view">
                <div style={{ flexDirection: 'column', display: 'flex', justifyContent: 'center' }}>
                    <div style={{ flexDirection: 'column', display: 'flex', background: '#289887', padding: '18px', margin: '30px 24px 10px 24px', color: '#FFFFFF', borderRadius: '4px', fontSize: '15px', fontWeight: 'bold' }}>以下问题请根据你最近三个月的身体感受回答，孕妇及16岁以下的人群不建议使用本测试。</div>

                    <div style={{ flexDirection: 'row', display: 'flex', margin: '10px 10px 10px 24px' }}>
                        <div style={{ overflow: 'scroll', alignSelf: 'center', background: '#FFFFFF', height: '500px', borderRadius: '4px',width:'100%' }}>
                            {
                                subject.map((item, index) => {
                                    return <div key={index} style={{ margin: '24px', overflow: 'hidden' }}>
                                        <Collapsible trigger={(index + 1) + '. ' + item.title} triggerClassName="mycollapsible" triggerOpenedClassName="mycollapsible">
                                            <div className="collapsible-item">
                                                <div
                                                    onClick={() => {
                                                        const { answer } = this.state;
                                                        answer.splice(index, 1, { "index": index, "val": 1 })
                                                        this.setState({ answer })
                                                    }}
                                                    style={{ display: 'flex', flex: 1, flexDirection: 'column', alignItems: 'center', justifyContent: 'center', background: `${this.state.answer[index] && this.state.answer[index].val === 1 ? '#289887' : ''}` }}>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 1 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500' }}>没有</div>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 1 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500', marginTop: '4px' }}>根本不</div>
                                                </div>
                                                <div
                                                    onClick={() => {
                                                        const { answer } = this.state;
                                                        answer.splice(index, 1, { "index": index, "val": 2 })
                                                        this.setState({ answer })
                                                    }}
                                                    style={{ display: 'flex', flex: 1, flexDirection: 'column', alignItems: 'center', justifyContent: 'center', background: `${this.state.answer[index] && this.state.answer[index].val === 2 ? '#289887' : ''}` }}>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 2 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500' }}>有时</div>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 2 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500', marginTop: '4px' }}>有些</div>
                                                </div>
                                                <div
                                                    onClick={() => {
                                                        const { answer } = this.state;
                                                        answer.splice(index, 1, { "index": index, "val": 3 })
                                                        this.setState({ answer })
                                                    }}
                                                    style={{ display: 'flex', flex: 1, flexDirection: 'column', alignItems: 'center', justifyContent: 'center', background: `${this.state.answer[index] && this.state.answer[index].val === 3 ? '#289887' : ''}` }}>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 3 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500' }}>经常</div>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 3 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500', marginTop: '4px' }}>相当</div>
                                                </div>
                                                <div
                                                    onClick={() => {
                                                        const { answer } = this.state;
                                                        answer.splice(index, 1, { "index": index, "val": 4 })
                                                        this.setState({ answer })
                                                    }}
                                                    style={{ display: 'flex', flex: 1, flexDirection: 'column', alignItems: 'center', justifyContent: 'center', background: `${this.state.answer[index] && this.state.answer[index].val === 4 ? '#289887' : ''}` }}>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 4 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500' }}>总是</div>
                                                    <div style={{ color: `${this.state.answer[index] && this.state.answer[index].val === 4 ? '#FFFFFF' : '#289887'}`, fontSize: '14px', fontWeight: '500', marginTop: '4px' }}>非常</div>
                                                </div>
                                            </div>
                                        </Collapsible>
                                        <div style={{ background: '#A7D1C2', flex: 1, display: 'flex', height: '1px', marginTop: '16px' }}></div>
                                    </div>
                                })
                            }
                        </div>
                        <img src="../static/icon_right_bg.png" style={{ width: '20px', height: '500px' }} />
                    </div>


                    <div style={{ alignSelf: 'center', display: 'flex', justifyContent: 'center', marginTop: '20px' }} className="button-view" onClick={() => {
                        this.requestAPI();
                    }}>
                        <div style={{ alignSelf: 'center', fontSize: '16px', color: '#FFFFFF', fontWeight: 700 }}>提交测试</div>
                    </div>
                </div>
            </div>
        )
    }

    requestAPI() {
        const { mobile } = this.props.location.state;

        var answer = ""
        this.state.answer.map((item) => {
            if (item.val == -1) {
                answer = "";
                return;
            } else {
                answer += item.group + item.val;
            }
        })
        console.log(111, this.state.answer)
        console.log('answer', answer)
        if (!answer) {
            Toast.show("您还有未填写的题目噢", 1);
            return;
        }

        var formData = new FormData();
        formData.append('answer', answer);
        formData.append('mobile', mobile);
        axios({
            headers: {
                'Content-Type': 'multipart/form-data'
            },
            method: 'post',
            url: 'http://wenquan.in-depth.top/wenquan/index/answer',
            data: formData
        }).then((res) => {
            console.log(res);
            if (res.status === 200) {
                var result = res.data;
                if (result.status == 0) {
                    Toast.show("提交成功", 1);
                    this.props.history.push({
                        pathname: '/Result',
                        state: { pagePath: 'Questionnaire', ...this.props.location.state },
                    });
                } else {
                    Toast.show(result.msg ? result.msg : "", 1);
                }
            }
        }).catch((err) => { })
    }
}