import React from "react";
import '../Select/index.css'
import { Toast } from 'antd-mobile';

export default class Select extends React.Component {
    constructor(props) {
        super(props);
        this.state =
        {
            mobile: ''
        }
    }
    render() {
        return (<div id="page-view">
            <div style={{ flexDirection: 'column', display: 'flex', justifyContent: 'center' }}>
                <div style={{ alignSelf: 'center', marginTop: '15%', marginBottom: '10px' }} >
                    <img src='../static/login_title.png' width={299} height={48} />
                </div>
                <div style={{ alignSelf: 'center', marginBottom: '20px' }} >
                    <img src='../static/login_dec.png' width={187} height={14} />
                </div>
                <div style={{ alignSelf: 'center', marginBottom: '10px' }} >
                    <img src='../static/login_logo.png' width={293} height={252} />
                </div>
                <div style={{ flexDirection: 'column', display: 'flex' }}>
                    <div style={{ margin: '0px 40px 14px 40px', fontSize: '14px', color: 'rgb(33,145,125)', fontWeight: 500 }}>查询您的测试结果</div>
                    <div style={{ margin: '0px 40px 30px 40px' }}>
                        <input
                            onChange={e => this.setState({ mobile: e.target.value })}
                            placeholder="请输入您的手机号"
                            style={{
                                width: '100%',
                                fontSize: '14px',
                                borderRadius: '6px',
                                background: '#FFFFFF',
                                height: '32px',
                                textAlign: 'left',
                                paddingLeft: '10px',
                                border: 0,
                                outline: 'none'
                            }} />
                    </div>
                    <div style={{ alignSelf: 'center', display: 'flex', justifyContent: 'center' }} className="button-view" onClick={() => {
                        const { mobile } = this.state;
                        
                        if (mobile.length != 11 && mobile.toString().sub(0, 1) != '1') {
                            Toast.show('手机号格式不正确', 1);
                            return;
                        }

                        this.props.history.push({
                            pathname: '/Result',
                            state: { pagePath: 'Select', ...this.state },
                        });
                        // window.open(url) //此处的url是全路径
                    }}>
                        <div style={{ alignSelf: 'center', fontSize: '16px', color: '#FFFFFF', fontWeight: 700 }}>立即查询</div>
                    </div>
                </div>
            </div>
        </div>)
    }

}