import React from "react";
import '../Report/index.css'
import 'antd-mobile/dist/antd-mobile.css';
import axios from 'axios'
import { Toast } from 'antd-mobile';

export default class Report extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            deqi: null,
            possibility: []
        }
        console.log('props', this.props.location.state)
        this.requestAPI();
    }
    render() {
        return (
            <div id="page-view">
                <div style={{ flexDirection: 'column', display: 'flex', justifyContent: 'center' }}>
                    <div style={{ alignSelf: 'center', marginTop: '15%', marginBottom: '10px' }} >
                        <img src='../static/icon_logo_title.png' width={299} height={33} style={{ alignSelf: 'center' }} />
                    </div>
                    <div style={{ alignSelf: 'center', marginBottom: '20px' }} >
                        <img src='../static/icon_logo_dec.png' width={237} height={25} style={{ alignSelf: 'center' }} />
                    </div>
                    <div style={{ flexDirection: 'column', display: 'flex' }}>

                        <div style={{ flexDirection: 'column', margin: '0px 14px 40px 14px', background: 'rgba(255, 255, 255, 0.4)', minHeight: '480px', borderRadius: '4px', display: 'flex', justifyContent: 'center', padding: '14px 4px 14px 4px' }}>
                            <div style={{ alignSelf: 'center', width: '98%', background: '#FFFFFF', minHeight: '400px', borderRadius: '4px', paddingTop: '44px' }}>
                                <div style={{ flexDirection: 'row', display: 'flex', margin: '0px 14px 20px 14px', borderRadius: '6px', padding: '8px', background: 'rgba(40, 152, 135, 1)', alignItems: 'center' }}>
                                    <img src='../static/report_icon.png' width={35} height={35} style={{ alignSelf: 'center' }} />
                                    <div style={{ marginLeft: '10px', fontSize: '18px', color: '#FFFFFF', fontWeight: 500 }}>先天健康信息采集报告</div>
                                </div>
                                <div style={{ fontSize: '15px', color: '#666666', margin: '0px 14px 20px 14px' }}>部位患病几率：</div>
                                {/* 进度 */}
                                {
                                    this.state.possibility ? Object.keys(this.state.possibility).map((key, index) => {
                                        console.log()
                                        return <div key={index} style={{ flexDirection: 'row', display: 'flex', margin: '0px 20px 20px 14px' }}>
                                            <div style={{ marginRight: '20px', fontSize: '15px', color: '#289887', fontWeight: 500, minWidth: '20%' }}>{key}</div>
                                            <div style={{ width: '100%', height: '20px', background: '#99CBB9', borderRadius: '10px' }}>
                                                <div style={{ width: `${this.state.possibility[key]}`, height: '20px', background: '#289887', borderRadius: '10px', display: 'flex', justifyContent: 'flex-end', paddingRight: '10px', alignItems: 'center' }}>
                                                    <div style={{ color: '#FFFFFF', fontSize: '12px' }}>{this.state.possibility[key]}</div>
                                                </div>
                                            </div>
                                        </div>
                                    }) : null
                                }


                                <div style={{ fontSize: '15px', color: '#666666', margin: '0px 14px 0px 14px' }}>先天得气旺弱：</div>
                                <div style={{ display: 'flex', flexWrap: 'wrap', margin: '0px 14px 20px 14px' }}>
                                    {
                                        this.state.deqi ? Object.keys(this.state.deqi).map((key, index) => {
                                            return <div key={index} style={{ background: '#289887', borderRadius: '15px', height: '31px', marginTop: '10px', marginRight: '10px', padding: '0px 10px 2px 10px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                                <div style={{ fontSize: '15px', color: '#FFFFFF' }}>{this.state.deqi[key]}</div>
                                            </div>
                                        }) : null
                                    }
                                </div>
                            </div>
                            <div style={{ alignSelf: 'center', display: 'flex', justifyContent: 'center', marginTop: '20px' }} className="button-view" onClick={() => {
                                this.props.location.state = {
                                    name: "kingkong",
                                    mobile: "18602242651",
                                    sex: [0],
                                    year: "2021",
                                    month: "08",
                                    date: "24",
                                    hour: "09",
                                }
                                this.props.history.push({
                                    pathname: '/Questionnaire',
                                    state: {...this.props.location.state },
                                });
                            }}>
                                <div style={{ alignSelf: 'center', fontSize: '16px', color: '#FFFFFF', fontWeight: 700 }}>深入体检</div>
                            </div>
                        </div>

                        <div style={{ alignSelf: 'center', marginTop: '0px', position: 'absolute' }} >
                            <img src='../static/icon_label.png' width={135} height={38} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    requestAPI() {
        const { name, mobile, sex, year, month, date, hour } = this.props.location.state;

        var formData = new FormData();
        formData.append('name', name);
        formData.append('mobile', mobile);
        formData.append('sex', sex);
        formData.append('year', year);
        formData.append('month', month);
        formData.append('date', date);
        formData.append('hour', hour);
        axios({
            headers: {
                'Content-Type': 'multipart/form-data'
            },
            method: 'post',
            url: 'http://wenquan.in-depth.top/wenquan/index/inborns',
            data: formData
        }).then((res) => {
            console.log(res);
            if (res.status === 200) {
                var result = res.data;
                if (result.status == 0) {
                    this.setState({
                        deqi: result.data.deqi,
                        possibility: result.data.possibility
                    })
                } else {
                    Toast.show(result.msg ? result.msg : "", 1);
                }
            }
        }).catch((err) => { })
    }

}