import React from "react";
import '../Login/index.css'
import { Picker, DatePicker, Toast } from 'antd-mobile';
import request from 'umi-request';
import moment from 'moment'
import axios from 'axios'

const nowTimeStamp = Date.now();
const now = new Date(nowTimeStamp);

export default class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            year: "",
            month: "",
            date: "",
            hour: "",
            birthday: "",
            name: "kingkong",
            mobile: "18602242651",
            sex: -1,
        }
    }

    render() {
        return (<div id="page-view">
            <div style={{ flexDirection: 'column', display: 'flex', justifyContent: 'center' }}>
                <div style={{ alignSelf: 'center', marginTop: '15%', marginBottom: '10px' }} >
                    <img src='../static/login_title.png' width={299} height={48} />
                </div>
                <div style={{ alignSelf: 'center', marginBottom: '20px' }} >
                    <img src='../static/login_dec.png' width={187} height={14} />
                </div>
                <div style={{ alignSelf: 'center', marginBottom: '10px' }} >
                    <img src='../static/login_logo.png' width={293} height={252} />
                </div>
                <div style={{ flexDirection: 'column', display: 'flex' }}>
                    <div style={{ margin: '0px 40px 14px 40px', fontSize: '12px', color: 'rgba(40, 152, 135, 1)', fontWeight: 500 }}>请填写基本信息，马上获取体检信息</div>
                    <div style={{ margin: '0px 40px 10px 40px' }}>
                        <div style={{ flexDirection: 'row', fontSize: '12px', color: 'rgb(17,140,115)', display: 'flex', borderRadius: '6px', alignItems: 'center', background: 'rgba(215, 233, 229, 1)', height: '30px', padding: '2px 14px 2px 14px', marginBottom: '14px' }}>
                            姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名&nbsp;&nbsp;|
                            <input
                                onChange={e => this.setState({ name: e.target.value })}
                                style={{
                                    flex: 1,
                                    fontSize: '14px',
                                    background: 'rgba(215, 233, 229, 1)',
                                    textAlign: 'left',
                                    paddingLeft: '6px',
                                    border: 0,
                                    color: 'rgb(17,140,115)',
                                    outline: 'none'
                                }} />
                        </div>
                        <div style={{ flexDirection: 'row', fontSize: '12px', color: 'rgb(17,140,115)', display: 'flex', borderRadius: '6px', alignItems: 'center', background: 'rgba(215, 233, 229, 1)', height: '30px', padding: '2px 14px 2px 14px', marginBottom: '14px' }}>
                            性&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;别&nbsp;&nbsp;|
                            <div style={{ flex: 1 }}>
                                <Picker
                                    cols={1}
                                    value={this.state.sex}
                                    data={[{ label: '女', value: 0 }, { label: '男', value: 1 }]}
                                    title="请选择性别"
                                    onOk={sex => this.setState({ sex })}
                                >
                                    <div
                                        style={{
                                            height: '15px',
                                            minWidth: '150px',
                                            fontSize: '14px',
                                            color: 'rgb(17,140,115)',
                                            textAlign: 'left|center',
                                            paddingLeft: '6px',
                                        }} >{this.state.sex != -1 ? (this.state.sex == 0 ? '女' : '男') : ''}</div>
                                </Picker>
                            </div>
                        </div>
                        <div style={{ flexDirection: 'row', fontSize: '12px', color: 'rgb(17,140,115)', display: 'flex', borderRadius: '6px', alignItems: 'center', background: 'rgba(215, 233, 229, 1)', height: '30px', padding: '2px 14px 2px 14px', marginBottom: '14px' }}>
                            出生日期&nbsp;&nbsp;|
                            <div style={{ flex: 1 }}>
                                <DatePicker
                                    title="请选择出生日期"
                                    mode="datetime"
                                    value={this.state.birthday ? this.state.birthday : now}
                                    onChange={birthday => this.setState({
                                        birthday: birthday,
                                        year: moment(birthday).format('YYYY'),
                                        month: moment(birthday).format('MM'),
                                        date: moment(birthday).format('DD'),
                                        hour: moment(birthday).format('HH'),
                                    })}
                                >
                                    <div
                                        style={{
                                            height: '15px',
                                            minWidth: '150px',
                                            fontSize: '14px',
                                            color: 'rgb(17,140,115)',
                                            textAlign: 'left|center',
                                            paddingLeft: '6px',
                                        }} >{this.state.birthday ? moment(this.state.birthday).format('YYYY-MM-DD') : ''}</div>
                                </DatePicker>
                            </div>
                        </div>
                        <div style={{ flexDirection: 'row', fontSize: '12px', color: 'rgb(17,140,115)', display: 'flex', borderRadius: '6px', alignItems: 'center', background: 'rgba(215, 233, 229, 1)', height: '30px', padding: '2px 14px 2px 14px', marginBottom: '14px' }}>
                            联系电话&nbsp;&nbsp;|
                            <div style={{ flex: 1 }}>
                                <input
                                    onChange={e => this.setState({ mobile: e.target.value })}
                                    style={{
                                        width: '100%',
                                        fontSize: '14px',
                                        background: 'rgba(215, 233, 229, 1)',
                                        textAlign: 'left',
                                        paddingLeft: '6px',
                                        border: 0,
                                        color: 'rgb(17,140,115)',
                                        outline: 'none'
                                    }} />
                            </div>
                        </div>
                    </div>
                    <div style={{ alignSelf: 'center', display: 'flex', justifyContent: 'center' }} className="button-view" onClick={() => {
                        // this.props.history.push("/Select")
                        // let url = document.URL + "/detail/" + projectId;
                        // window.open(url) //此处的url是全路径
                        this.requestAPI();
                    }}>
                        <div style={{ alignSelf: 'center', fontSize: '16px', color: '#FFFFFF', fontWeight: 700 }}>开始体检</div>
                    </div>
                </div>
            </div>
        </div>)
    }

    onChange(date, dateString) {
        console.log(date, dateString)
    }

    requestAPI() {
        const { name, mobile, sex, birthday } = this.state;
        if (!name) {
            Toast.show('请输入您的姓名', 1);
            return;
        }
        if (sex == -1) {
            Toast.show('请选择性别', 1);
            return;
        }
        if (!birthday) {
            Toast.show('请选择出生日期', 1);
            return;
        }
        if (!mobile) {
            Toast.show('请输入手机号', 1);
            return;
        }
        if (mobile.length != 11 && mobile.toString().sub(0, 1) != '1') {
            Toast.show('手机号格式不正确', 1);
            return;
        }

        var formData = new FormData();
        formData.append('name', name);
        formData.append('mobile', mobile);
        formData.append('sex', sex[0]);
        formData.append('birthday', moment(birthday).format('YYYY-MM-DD'));
        axios({
            headers: {
                'Content-Type': 'multipart/form-data'
            },
            method: 'post',
            url: 'http://wenquan.in-depth.top/wenquan/index/information',
            data: formData
        }).then((res) => {
            console.log(res);
            if (res.status === 200) {
                var data = res.data;
                if (data.status == 0) {
                    Toast.show("记录成功", 1);
                    this.state = {
                        name: "kingkong",
                        mobile: "18602242651",
                        sex: [0],
                        year: "2021",
                        month: "08",
                        date: "24",
                        hour: "09",
                    }
                    this.props.history.push({
                        pathname: '/Report',
                        state: { ...this.state },
                    });
                } else {
                    Toast.show(data.msg ? data.msg : "", 1);
                }
            }
        }).catch((err) => { })
    }
}